	import {  
        mapMutations  
    } from 'vuex';
	import api from '@/common/vmeitime-http/'

	export default{
		data(){
			return {
				username: '',
				password: '',
				logining: false
			}
		},
		onLoad(){
			
		},
		methods: {
			...mapMutations(['login']),
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			navBack(){
				uni.navigateBack();
			},
			tologin(){
				uni.navigateTo({
					url: '/pages/public/login'
				})
			},
			async toreg(){
				this.logining = true;
				const {username, password} = this;
				const sendData = {
					username,
					password
				};
				if(sendData.username == '' && !/^1[3|4|5|6|7|8|9][0-9]\d{8}$/.test(sendData.username)) {
					uni.showToast({title: '请输入正确的手机号吗', icon: 'none'})
					return false;
				} else if (sendData.password == '') {
					uni.showToast({title: '请输入密码', icon: 'none'})
					return false;
				}
				let homeurl = '/auth/register';
				api.httppost(homeurl, sendData).then((res) => {
					let respons = res.data;
					if(respons.errno == 0) {
						
					}
					uni.showToast({title: res.data.errmsg, icon: 'none'})
					uni.hideLoading()
				}).catch((err) => {
					uni.hideLoading()
					uni.showToast({title: res.data.errmsg, icon: 'none'})
				})
				
			}
		},

	}
